<template>
  <header class="header" >
    <h1>todos</h1>
    <input
      @keyup.13="addTodo"
      v-model="val"
      class="new-todo"
      placeholder="请输入内容"
    />
  </header>
</template>

<script>
  export default {
    name: 'TodoHeader',
    data () {
      return {
        val: ''
      }
    },
    methods: {
      addTodo(){
        if(this.val.trim() !== ''){
          this.$store.commit('addTodoMuta',{id:Date.now()+''+Math.random(),title:this.val,checked:false})
          this.val = ''
        }
      }
    }
  }
</script>

<style scoped>
  .header h1 {
    position: absolute;
    top: -155px;
    width: 100%;
    font-size: 100px;
    font-weight: 100;
    text-align: center;
    color: rgba(175, 47, 47, 0.15);
    -webkit-text-rendering: optimizeLegibility;
    -moz-text-rendering: optimizeLegibility;
    text-rendering: optimizeLegibility;
  }
  .new-todo{
    position: relative;
    margin: 0;
    width: 100%;
    font-size: 24px;
    font-family: inherit;
    font-weight: inherit;
    line-height: 1.4em;
    border: 0;
    outline: none;
    color: inherit;
    padding: 6px;
    border: 1px solid #999;
    box-shadow: inset 0 -1px 5px 0 rgba(0, 0, 0, 0.2);
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-font-smoothing: antialiased;
    font-smoothing: antialiased;
  }
  .new-todo {
    padding: 16px 16px 16px 60px;
    border: none;
    background: rgba(0, 0, 0, 0.003);
    box-shadow: inset 0 -2px 1px rgba(0,0,0,0.03);
  }
  .new-todo::-webkit-input-placeholder {
    font-style: italic;
    font-weight: 300;
    color: #e6e6e6;
  }
  .new-todo::-moz-placeholder {
    font-style: italic;
    font-weight: 300;
    color: #e6e6e6;
  }
  .new-todo::input-placeholder {
    font-style: italic;
    font-weight: 300;
    color: #e6e6e6;
  }
</style>



